.subscriptions {
  background-color: #f8f9fc;
}

.subscriptions__wrap {
  @include wrap-desktop;
  padding-top: 116px;
  padding-bottom: 80px;
}

.subscriptions__title {
  color: $dark-blue-color;
  font-family: $bebas;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  margin: 0 auto 42px;
  text-align: center;
}

.subscriptions__controls-wrap {
  display: flex;
  justify-content: center;
}

.subscriptions__controls-list {
  @include list-default;
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  border-bottom: 2px solid #e4e4e4;
  padding-bottom: 13px;
}

.subscriptions__controls-list li {
  margin-right: 46px;
}

.subscriptions__controls-list li:last-child {
  margin-right: 0;
}

.subscriptions__item-month {
  color: $dark-blue-color;
  font-family: $roboto-condensed;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  cursor: pointer;
  padding-bottom: 13px;
}

.subscriptions__item-month--active {
  border-bottom: 2px solid #d10a42;
}

.subscriptions__tabs-list {
  @include list-default;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 39px;
}

.subscriptions__tabs-list li {
  display: block;
  background: $white-color;
  width: calc(33.33% - 27px);
  margin-right: 40px;
  margin-bottom: 40px;
  padding: 55px 30px 59px;
  position: relative;
  border: 7px solid transparent;

  &:hover {
    border-image: linear-gradient(to bottom right, #d10a42, #00359f);
    border-image-slice: 1;
    .subscriptions__buy-link {
      border: none;
      background: linear-gradient(to right, #d10a42, #00359f);
      color: $white-color;
    }
  }
}

.subscriptions__tabs-list li:nth-child(3n) {
  margin-right: 0;
}

.subscriptions__tabs-list h3 {
  font-family: $roboto-condensed;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  margin: 0;
  text-align: center;
}

.subscriptions__tabs-list p {
  font-family: $roboto-condensed;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  margin: 10px 0 0;
  text-align: center;
}

.subscriptions__bg-price {
  font-family: $roboto-condensed;
  font-weight: 700;
  font-size: 100px;
  line-height: 100px;
  display: block;
  text-align: center;
  margin-bottom: -80px;
  margin-top: 45px;
  color: #e8e8e8;
  opacity: 0.48;
}


.subscriptions__price-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 34px;
  position: relative;
  z-index: 10;
}

.subscriptions__price {
  font-family: $roboto-condensed;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
  color: #103aae;
  margin-right: 14px;
}

.subscriptions__buy-link {
  display: block;
  font-family: $roboto-condensed;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 2px solid $dark-blue-color;
  text-align: center;
  width: 206px;
  margin: 0 auto;
  padding: 24px 0;
  cursor: pointer;
  color: $dark-blue-color;
}

.hide {
  display: none;
}

.show {
  display: flex;
}

@media (max-width: $tablet-width) {
  .subscriptions__wrap {
    @include wrap-tablet;
    padding: 96px 163px 60px;
  }

  .subscriptions__title {
    font-size: 42px;
    line-height: 42px;
    margin: 0 auto 36px;
  }

  .subscriptions__controls-list li {
    margin-right: 54px;
  }

  .subscriptions__controls-list {
    padding-bottom: 12px;
  }

  .subscriptions__bg-price {
    margin-top: 38px;
    margin-bottom: -72px;
  }

  .subscriptions__item-month {
    font-size: 18px;
    line-height: 18px;
  }

  .subscriptions__tabs-list {
    flex-direction: column;
    margin-top: 45px;
  }

  .subscriptions__tabs-list li {
    width: 100%;
    margin-right: 0;
    padding: 55px 30px 60px;
  }
}

@media (max-width: $mobile-width) {
  .subscriptions__wrap {
    @include wrap-mobile;
    padding: 67px 15px 34px;
  }

  .subscriptions__title {
    font-size: 32px;
    line-height: 32px;
    margin: 0 auto 26px;
  }

  .subscriptions__controls-list li {
    margin-right: 45px;
  }

  .subscriptions__item-month {
    font-size: 16px;
    line-height: 16px;
  }

  .subscriptions__controls-list {
    padding-bottom: 11px;
  }

  .subscriptions__tabs-list {
    margin-top: 35px;
  }

  .subscriptions__tabs-list li {
    padding: 44px 30px 47px;
    margin-bottom: 35px;
  }

  .subscriptions__tabs-list h3 {
    font-size: 22px;
    line-height: 24px;
  }

  .subscriptions__price {
    font-size: 48px;
    line-height: 72px;
    margin-right: 12px;
  }

  .subscriptions__price-ruble {
    width: 24px;
    height: 33px;
  }

  .subscriptions__bg-price {
    font-size: 80px;
    line-height: 80px;
    margin-top: 28px;
    margin-bottom: -55px;
  }

  .subscriptions__price-container {
    margin-bottom: 30px;
  }
}
